.login {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  width: 100vw;
  z-index: 100;
}

.video-container {
  height: 100vh;
  position: relative;
  /* width: 100vw; */
  z-index: -1;
}

.video-container video {
  height: 100%;
  object-fit: cover;
  position: fixed;
  width: 100%;
  z-index: -1;
}

.login__logo {
  margin-top: 2rem;
  max-width: 80vw;
  width: 550px;
  z-index: 100;
}

/* https://codersblock.com/blog/creating-glow-effects-with-css/ */
.login__glow {

  /* background-color: white; */
  border-radius: 50%;

  /* box-shadow: 0 0 60px 30px white ,
      0 0 100px 60px rgb(9, 255, 0) ,
      0 0 140px 90px rgb(64, 255, 0); */
  height: 200px;
  margin-left: -1rem;
  margin-top: -160px;
  max-width: 50vw;
  position: absolute;
  width: 250px;
  z-index: 100;
}

.login__form {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  z-index: 100;
}

.App {
  text-align: center;
}

.App-logo {
  pointer-events: none;
  width: 40vw;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

@media ( prefers-reduced-motion : no-preference ) {

  /* .App-logo {
    animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  } */
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@keyframes shake {

  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
